import { useEffect, useState } from 'react';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import AddMaintenanceCenterForm from '../forms/addMaintenanceCenter/AddMaintenanceCenterForm';
import Heading from '../components/editMaintenaceCenter/Heading';
import { useParams } from 'react-router-dom';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { MaintenanceCenter } from '@/types/maintenanceCenter';

const MaintenanceCenterEditPage = () => {
    const params = useParams();
    const { id } = params;
    const [maintenanceCenter, setMaintenanceCenter] = useState<MaintenanceCenter | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchMaintenanceCenter = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.maintenanCenter.show + id,
                });
                setMaintenanceCenter(response.data.data);
            } catch (error) {
                setError('Failed to load maintenanceCenter details');
            } finally {
                setLoading(false);
            }
        };

        if (id) {
            fetchMaintenanceCenter();
        } else {
            setError('Invalid maintenanceCenter ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!maintenanceCenter) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Car not found</h2>
            </div>
        );
    }

    return (
        <div className="mt-6 w-full">
            <Heading />
            <AddMaintenanceCenterForm maintenanceCenter={maintenanceCenter} />
        </div>
    );
};

export default MaintenanceCenterEditPage;
